.mfp-content {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 15;
    display: grid;
    justify-content: center;
    grid-template-columns: 100%;
    background-color: #2f2f2f;
    display: none;
}

#consult {
    font-size: 17px;
    display: grid;
    background-color: white;
    grid-template-columns: 80%;
    justify-content: center;
    justify-self: center;
    align-self: center;
    width: 80%;
    top: 10vh;
    height: 80vh;
}

.form {
    display: grid;
    align-self: center;

    &__inner {
        display: grid;
        grid-template-rows: repeat(2, 45px) repeat(2, auto);
        align-self: center;
        row-gap: 40px;
    }

    &__content {
        display: grid;
        grid-template-columns: repeat(2, auto);
    }

    &__colLeft {
        display: grid;
        grid-template-columns: repeat(2, auto);
    }

    &__footer {
        display: grid;
        row-gap: 40px;
    }

    &__title {
        display: grid;
        font-weight: 700;
        font-size: 25px;

        &_2 {
            font-size: 3.5rem;
            font-weight: 300;
        }
    }

    &-agree {
        display: grid;
        grid-template-columns: 10% auto;
        width: 80%;
        justify-self: center;
    }

    &__success {
        display: none;
    }

    &__error {
        display: none;
    }
}

.input-wrap {
    -webkit-tap-highlight-color: transparent;
    --font-family-sans-serif: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    --font-family-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    color: #2f2f2f;
    -webkit-appearance: none;
    box-shadow: none;
    border-radius: 0;
    margin: 0;
    font-family: inherit;
    line-height: inherit;
    overflow: visible;
    box-sizing: border-box;
    width: 100%;
    height: 4.4rem;
    background: 0 0;
    border: 0;
    outline: 0;
    position: relative;
    z-index: 2;
    font-size: 1.5rem;
    font-weight: 700;
}

.js-input {
    border: 0;
    font-size: 16px;
}

.ignore {
    border: 0;
    font-size: 16px;
}

.pWeight {
    font-weight: 700;
}

.agree-check_box_item {
    justify-self: center;
    align-self: center;
}

.close-button {
    height: 50px;
    width: 50px;
    display: grid;
    background-color: #2f2f2f;
    color: white;
    justify-content: center;
    align-content: center;
    position: absolute;
    top: 10%;
    right: 10%;
}

center {
    max-height: 40px;
    height: auto;
}

.js-submit {
    cursor: pointer;
}

.js-submit:nth-last-child(1) {
    width: 20%;
    font-size: 18px;
    justify-self: center;
    cursor: pointer;
}

.pColor {
    color: #3d4f8e;
    font-weight: 700;
}