.flat {
    position: relative;
    top: 15.5rem;
    display: grid;
    justify-content: center;
    grid-template-columns: 90%;
    row-gap: 30px;

    &__title {
        display: grid;
        font-size: 40px;
        font-weight: 700;
        justify-content: center;
    }

    &-icons {
        display: grid;
        grid-template-columns: repeat(3, 60px);
        width: 30%;
        justify-content: space-around;
        justify-self: center;

        &__item {
            max-height: 60px;
            border-radius: 50%;
            box-shadow: none;
            transition: all 0.3s;

            &:hover {
                box-shadow: 0px 5px 14px rgb(0 29 46 / 20%);
            }
        }
    }

    &-main {
        display: grid;
        grid-template-columns: 40% 40%;
        justify-content: space-around;

        &-sheme {
            position: relative;

            &__pin {
                position: absolute;
                left: 70%;
                top: 16%;
            }
        }

        &-descr {
            display: grid;
            align-content: flex-start;

            &-text {
                display: grid;

                &__title {
                    font-size: 30px;
                }

                &__text {
                    font-size: 20px;
                }
            }
        }
    }
}