.pay {
    font-family: 'electra';
    position: relative;
    top: 15.5rem;
    font-size: 16px;
    margin-top: 50px;
    margin-bottom: 50px;

    &-col {
        display: grid;
        row-gap: 30px;
    }

    &-links {
        display: grid;
        grid-template-columns: repeat(3, auto);
        color: #999;
        width: 20%;
        align-items: center;

        &__home {
            color: #999;
            text-decoration: none;
        }
    }

    &__title {
        color: #444;
        font-size: 42px;
        font-weight: 700;
    }

    &-form {
        padding-left: 10px;
        padding-top: 10px;
        display: grid;
        row-gap: 30px;

        &__text {
            padding-left: 55px;
            padding-right: 20px;
            color: #999;
        }

        &-top {
            display: grid;
            grid-template-columns: 5% 30%;
            font-size: 20px;
            align-items: center;

            &__num {
                display: grid;
                height: 50px;
                width: 50px;
                justify-content: center;
                align-content: center;
                border: 1px solid #0050B4;
                border-radius: 50%;
            }

           
        }
        &-body {
            display: grid;
            justify-content: space-around;
            grid-template-columns: repeat(2, 40%);
            row-gap: 20px;

            &-col {
                display: grid;
                grid-template-rows: repeat(2, auto);

                &__name {
                    color: #999;
                    position: relative;
                    top: 25px;
                    transition: all 0.1s;
                }

                &__field {
                    background: none;
                    border: 0;
                    border-bottom: 1px solid black;
                    z-index: 5;

                    &:focus-visible {
                        outline: 0;
                    }
                }
            }

            &__button {
                display: grid;
                grid-column: span 2;
                width: 40%;
                justify-self: center;
                height: 45px;
                font-size: 16px;
            }
        }
    }

    &-form, &-recv {
        background-color: #faf9f7;
    }

    &-recv {
        padding-left: 10px;
        padding-top: 10px;
        display: grid;
        grid-template-rows: repeat(2, auto);
        row-gap: 30px;
    }
}

#send {
    font-family: 'electra';
    font-size: 2rem;
}