.faq {
    position: relative;
    top: 15.5rem;
    font-size: 2.1rem;
    font-weight: 700;
    margin-top: 60px;
    margin-bottom: 60px;

    &__title {
        font-size: 42px;
        margin-bottom: 30px;
    }
}

.accordion-body {
    font-weight: 400;
}

.accordion-button:hover {
    color: #0c63e4;
}

.accordion-item:hover {
    border-color: #0c63e4;
}

#buttonOne, #buttonTwo,#buttonThree, #buttonFour {
    font-size: 2rem;
    font-weight: 700;
    padding: 15px;
    
    &:hover {
        border-color: #0c63e4;
    }
}

#buttonOne {
    border-top: 1px solid rgba(0, 0, 0, 0.125);;
}

.accordion-button:not(.collapsed) {
    background-color: white !important;
    color: black !important;
}