.tariff {
    font-family: 'electra';
    position: relative;
    top: 15.5rem;
    display: grid;
    justify-content: center;
    margin-top: 31.25rem;
    display: grid;
    grid-template-rows: auto auto;
    row-gap: 50px;
    font-size: 2.5rem;
    grid-template-columns: 90%;

    &-left {
        display: grid;
        grid-template-rows: repeat(2, auto);
        border: 1px #888 solid;
        row-gap: 40px;
        padding-top: 20px;

        &-top {
            display: grid;
            grid-template-columns: 45% 45%;
            justify-content: space-around;
            align-items: center;
            width: 90%;
            justify-self: center;
            padding-bottom: 10rem;
            border-bottom: 1px solid #b81705;

            &-form {
                display: grid;
                grid-template-rows: 2fr 3fr 2fr 3fr;

                &-selection {
                    font-size: 2.5rem !important;
                    border-top: none !important;
                    border-left: none !important;
                    border-right: none !important;
                    font-weight: 700 !important;
                    border-bottom: 2px solid #888 !important;
                    margin-bottom: 20px;

                    &__option {
                        font-size: 2.5rem;
                        font-weight: 700;
                    }
                }
            }

            & img {
                width: 520px;
                position: relative;
                top: 40px;
            }
        }

        &-bottom {
            display: grid;
            grid-template-columns: repeat(2, 40%);
            width: 90%;
            justify-self: center;
            justify-content: space-around;
            row-gap: 20px;

            &__title {
                grid-column: span 2;
                font-weight: 700;
            }
        }
    }

    &__title {
        font-size: 5.25rem;
        font-weight: 700;
        text-align: center;
    }

    &-container {
        justify-self: center;
        display: grid;
        width: 90%;
        grid-template-columns: 73% 25%;
        justify-content: space-between;
    }

    &-right {
        display: grid;
        grid-template-rows: repeat(4, auto);
        max-height: 70%;
        border: #b81705 1px solid;
        grid-template-columns: 80%;
        justify-content: center;
        align-items: center;

        &__title {
            font-size: 30px;
        }

        &-calc {
            display: grid;
            grid-template-rows: repeat(3, auto);
            grid-template-columns: repeat(2, auto);
            font-size: 2rem;

            &__name {
                font-weight: normal;
            }

            &__price {
                font-weight: 700;
                text-align: end;
            }
        }

        &__button {
            height: 60%;
            width: 60%;
            justify-self: center;
            border-radius: 30px !important;
            font-size: 2rem !important;
        }

        &__cansel {
            cursor: pointer;
            justify-self: center;
            text-decoration: none;
        }
    }
}

#equipment {
    margin-top: 20px;
    padding-top: 10px;
    border-top: 1px solid #888;

    &__price {
        margin-top: 20px;
        padding-top: 10px;
        border-top: 1px solid #888;
    }
}