@use 'fonts';
@use 'header';
@use 'banner';
@use 'protection';
@use 'how-it-works';
@use 'tariff';
@use 'form';
@use 'house';
@use 'flat';
@use 'numbers';
@use 'faq';
@use 'footer';
@use "pay";

:root {
    --secondaryGray: #B1B1B1;
    --primaryBlack: #1C1C1C;
    --primaryYellow: #FFD500;
    --secondaryDarkYellow: #E6C000;
    --primaryBlue: #0050B4;
    --secondaryDarkBlue: #0048A2;
    --secondaryLightBlue: #E6EEF7;
}

body {
    margin: 0;
}

html {
    font-size: 8px;
}

a {
    text-decoration: none;
}

.up_arrow {
    width: 50px;
    height: 50px;
    background: #f5f5f5;
    bottom: 80px;
    color: #444;
    cursor: pointer;
    left: 40px;
    position: fixed;
    z-index: 10;
    display: grid;
    justify-content: center;
    align-content: center;
    font-size: 24px;
    text-decoration: none;
    border-radius: 2px;
    font-weight: 700;

    &:hover {
        background: #e9ebec;
        text-decoration: underline;
    }
}

a.anchor {
    position: relative;
    top: -20rem;
}