.header {
    width: 100%;
    display: grid;
    height: 15.5rem;
    font-family: 'electra';
    grid-template-rows: 3fr 5fr;
    position: fixed;
    top: 0;
    z-index: 2;

    &-top {
        display: grid;
        font-size: 1.4rem;
        grid-template-columns: 250px;
        background-color: #2f2e33;

        &-container {
            display: grid;
            grid-template-columns: repeat(2, auto);
            align-content: center;
            margin-left: 21px;

            &__account{
                color: #fff;
                position: relative;
                line-height: 2rem;
                padding-left: 21px;
                align-content: center;

                &::before {
                    left: 0;
                    display: block;
                    position: absolute;
                    content: '';
                    width: 3.4rem;
                    height: 2.5rem;
                    background: url(../images/header-top-account__pic.svg) no-repeat;
                }

                &:hover {
                    color: #a3a3a3;
                }
            }

            &__payment{
                color: #fff;
                position: relative;
                line-height: 2rem;
                padding-left: 21px;
                align-content: center;

                &::before {
                    left: 0;
                    display: block;
                    position: absolute;
                    content: '';
                    width: 3.4rem;
                    height: 2.5rem;
                    background: url(../images/header-top-payment__pic.svg) no-repeat;
                }

                &:hover {
                    color: #a3a3a3;
                }
            }
        }
    }

    &-bottom {
        display: grid;
        grid-template-columns: 45.3rem auto;
        background-color: white;

        &__logo {
            align-self: center;
            display: block;
            width: 29.3rem;
            height: 7.4rem;
            background: url('../images/logo.svg') no-repeat;
            background-size: contain;
            margin-left: 30px;
        }

        &-list {
            display: grid;
            grid-template-columns: repeat(4, auto);
            align-content: center;

            &__item {
                font-size: 2.3rem;
                color: #2f2e33;
                transition: all 0.2s;
                font-weight: 600;
                text-decoration: none;

                &:hover {
                    color: #005CA9;
                }
            }
        }
    }
}