.house {
    top: 15.5rem;
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 41%);
    justify-content: center;
    margin-top: 15.5rem;
    margin-bottom: 9.5rem;
    column-gap: 40px;

    &-text {
        display: grid;
        align-content: center;
        row-gap: 30px;

        &__title {
            font-size: 32px;
            font-weight: 700;
        }

        &__text {
            font-size: 18px;
            font-weight: 400;
        }
    }
}