.numbers {
    position: relative;
    top: 15.5rem;

    &-col {
        display: grid;
        row-gap: 20px;

        &__num {
            font-size: 5rem;
            color: #0050B4;
        }

        &__title {
            font-size: 2.5rem;
            color: #0050B4;
        }

        &__text {
            font-size: 2rem;
        }
    }
}