.banner {
    position: relative;
    top: 15.5rem;
    display: grid;
    min-height: 55rem;
    font-family: 'electra';
    grid-template-columns: repeat(2, 1fr);
    background: #2E2E33;
    color: white;
    justify-content: space-around;

    &-left {
        display: grid;
        justify-content: center;
        padding-left: 15rem;
        height: 75%;
        align-self: center;

        &__title {
            font-size: 40px;
        }

        &__tariff {
            font-size: 20px;
        }

        & p {
            font-size: 15px;
        }
    }

    &-right {
        background: url(../images/banner__bg.jpg) no-repeat right;
        background-size: contain;
        display: grid;


        &__img {
            position: relative;
            top: 50px;
            left: -30px;
            max-width: 600px;
            align-self: flex-end;
        }
    }
}