.protection {
    font-family: 'electra';
    margin-top: 10rem;
    top: 15.5rem;
    display: grid;
    position: relative;
    grid-template-rows: repeat(2, auto);
    color: #2E2E33;
    row-gap: 70px;
    margin-bottom: 60px;

    &-title {
        text-align: center;
        width: 60%;
        justify-self: center;

        &__title {
            font-size: 35px;
        }

        &__subtitle {
            font-size: 15px;
        }
    }

    &-items {
        justify-self: center;
        width: 80%;
        display: grid;
        grid-template-columns: repeat(3, 27%);
        justify-content: space-between;
        grid-template-rows: repeat(2, auto);
        row-gap: 70px;

        &-item {
            display: grid;
            grid-template-rows: 60px repeat(2, auto);
            row-gap: 23px;

            &__title {
                font-size: 18px;
                font-weight: 700;
            }

            &__text {
                font-size: 15px;
            }
        }
    }
}